/**
 * Internal dependencies
 */
import { getAlternative } from './alternative';
import { addQueryArgs } from '../../utils/url';
import type { Settings } from '../types';

export function getExpectedAlternativeUrl( settings: Settings ): string {
	const { numOfAlternatives, untestedUrl } = settings;
	const alternative = getAlternative( settings );
	const nab = alternative % numOfAlternatives;

	const pids = settings.postUrls.map( ( { postId } ) => postId );
	const urls = settings.postUrls.map( ( { url } ) => url );
	const idx = nab % urls.length;
	const url =
		! pids.length || pids[ idx ] === settings.postId
			? untestedUrl
			: urls[ idx ];

	return numOfAlternatives ? addQueryArgs( url, { nab } ) : url;
} //end getExpectedAlternativeUrl()
