/**
 * Internal dependencies
 */
import preloadQueryArgs from '../helpers/preload-query-args';

import { doesVisitorParticipate } from '../../utils/participation';
import { updateSegmentationSettings } from '../../utils/segments';
import { doSingleAction } from '../../utils/actions';
import {
	addQueryArgs,
	getQueryArgs,
	removeArgsAndHash,
	removeQueryArgs,
} from '../../utils/url';

import type { Settings } from '../types';

export function showPage( settings: Settings, targetUrl: string ): void {
	const { isTestedPostRequest } = settings;
	if ( isTestedPostRequest ) {
		try {
			window.history.replaceState( {}, '', targetUrl );
		} catch ( e ) {} //end try
		validateCurrentPage( settings );
		return;
	} //end if

	const { ignoreTrailingSlash, currentUrl } = settings;
	if ( areUrlsEqual( ignoreTrailingSlash, targetUrl, currentUrl ) ) {
		validateCurrentPage( settings );
	} else if ( isCurrentUrlJustMissingNab( settings, targetUrl ) ) {
		window.history.replaceState( {}, '', targetUrl );
		validateCurrentPage( settings );
	} else if ( isAlternativeRequestNoLongerValid( settings ) ) {
		stayWithNoTestingArgs();
		validateCurrentPage( settings );
	} else {
		redirect( settings, targetUrl );
	} //end if
} //end showPage()

// =======
// HELPERS
// =======

function validateCurrentPage( settings: Settings ) {
	if ( doesVisitorParticipate( settings ) ) {
		void updateSegmentationSettings( settings, 'future' );
		preloadQueryArgs( settings.preloadQueryArgUrls );
	} //end if
	cleanTestingQueryArgs( settings );
	doSingleAction( 'valid-content' );
} //end validateCurrentPage()

function redirect( settings: Settings, targetUrl: string ) {
	const { referrerParam } = settings;
	targetUrl = addMissingArguments( targetUrl, referrerParam );
	targetUrl = maybeAddHash( targetUrl );

	if ( ! document.location.replace ) {
		document.location.href = targetUrl;
		return;
	} //end if
	document.location.replace( targetUrl );
} //end redirect()

function stayWithNoTestingArgs() {
	const url = removeQueryArgs(
		document.location.href,
		'nab',
		'nabforce',
		'nab_experiments_with_page_view'
	);
	try {
		window.history.replaceState( {}, '', url );
	} catch ( e ) {} //end try
} //end stayWithNoTestingArgs()

function isCurrentUrlJustMissingNab( settings: Settings, targetUrl: string ) {
	const { nab: nabarg } = getQueryArgs( targetUrl );
	if ( undefined === nabarg ) {
		return false;
	} //end if

	const { currentUrl } = settings;
	const cleanTargetUrl = removeQueryArgs( targetUrl, 'nab' );
	if ( currentUrl !== cleanTargetUrl ) {
		return false;
	} //end if

	const nab = Number.parseInt( nabarg );
	if ( ! nab ) {
		return true;
	} //end if

	return nab < Object.values( settings.postUrls ).length;
} //end isCurrentUrlJustMissingNab()

function getReferrer( referrerParam: string ) {
	const query = document.location.search.replace( /^\?/, '' );
	const params = query.split( '&' ).reduce( ( memo, param ) => {
		const [ name, ...value ] = param.split( '=' );
		memo[ name ] = value.join( '=' );
		return memo;
	}, {} );

	if ( params[ referrerParam ] ) {
		return decodeURIComponent( params[ referrerParam ] );
	} //end if

	const referrer = document.referrer;
	const currentUrl = document.location.href;
	if ( isSameDomain( currentUrl, referrer ) ) {
		return false;
	} //end if

	return referrer;
} //end getReferrer()

function isSameDomain( oneUrl = '', anotherUrl = '' ) {
	const clean = ( x: string ) =>
		x.replace( /^https?:\/\//, '' ).replace( /\/.*$/, '' );
	return clean( oneUrl ) === clean( anotherUrl );
} //end isSameDomain()

function cleanTestingQueryArgs( settings: Settings ) {
	let url = document.location.href;
	url = removeQueryArgs( url, 'nabforce', 'nab_experiments_with_page_view' );

	if ( settings.hideQueryArgs ) {
		url = removeQueryArgs( url, 'nab' );
	} //end if

	try {
		window.history.replaceState( {}, '', url );
	} catch ( e ) {} //end try
} //end cleanTestingQueryArgs()

function areUrlsEqual(
	ignoreTrailingSlash = false,
	oneUrl = '',
	anotherUrl = ''
) {
	let oneCleanUrl = removeArgsAndHash( oneUrl );
	let anotherCleanUrl = removeArgsAndHash( anotherUrl );

	if ( ignoreTrailingSlash ) {
		oneCleanUrl = `${ oneCleanUrl }/`.replace( /\/\/$/, '/' );
		anotherCleanUrl = `${ anotherCleanUrl }/`.replace( /\/\/$/, '/' );
	} //end if

	if ( oneCleanUrl !== anotherCleanUrl ) {
		return false;
	} //end if

	const oneArgs = getQueryArgs( oneUrl );
	const anotherArgs = getQueryArgs( anotherUrl );

	const oneArgKeys = Object.keys( oneArgs );
	const anotherArgKeys = Object.keys( anotherArgs );
	if ( oneArgKeys.length !== anotherArgKeys.length ) {
		return false;
	} //end if

	for ( const key of oneArgKeys ) {
		if ( ! anotherArgKeys.includes( key ) ) {
			return false;
		} //end if
	} //end for

	for ( const key of oneArgKeys ) {
		if ( oneArgs[ key ] !== anotherArgs[ key ] ) {
			return false;
		} //end if
	} //end if

	return true;
} //end areUrlsEqual()

function addMissingArguments( url: string, referrerParam: string ) {
	const expectedArguments = getQueryArgs( url );
	const currentArguments = getQueryArgs( document.location.search );

	delete currentArguments.nab;
	delete currentArguments.nabforce;

	url = addQueryArgs( url, {
		...currentArguments,
		...expectedArguments,
	} );

	if ( ! referrerParam ) {
		return url;
	} //end if

	let referrer = getReferrer( referrerParam );
	if ( ! referrer ) {
		return url;
	} //end if

	referrer = removeQueryArgs( referrer, referrerParam );
	return addQueryArgs( url, { [ referrerParam ]: referrer } );
} //end addMissingArguments()

function maybeAddHash( url: string ) {
	if ( -1 !== url.indexOf( '#' ) ) {
		return url;
	} //end if

	return url + document.location.hash;
} //end maybeAddHash()

function isAlternativeRequestNoLongerValid( settings: Settings ) {
	const currentArguments = getQueryArgs( document.location.search );
	const isAlternativeRequest = Object.keys( currentArguments ).includes(
		'nab'
	);
	return 0 === settings.numOfAlternatives && isAlternativeRequest;
} //end isAlternativeRequestNoLongerValid()
