/**
 * External dependencies
 */
import type { ExperimentId, TrackEvent } from '@nab/types';

/**
 * Internal dependencies
 */
import { getCookie, setCookie } from '../utils/cookies';
import type { GoalIndex, Settings } from '../tracking/regular/types';

type ExpsWithViews = Record< ExperimentId, Timestamp >;
type Timestamp = number;

export function isStagingSite(): boolean {
	return !! getCookie( 'nabIsStagingSite' );
} //end isStagingSite()

export function updateExperimentsWithPageViews(
	events: ReadonlyArray< TrackEvent >,
	settings: Settings
): void {
	removeOldExperimentsFromCookie( settings.experiments );

	events = events.filter( ( event ) => 'visit' === event.kind );
	if ( ! events.length ) {
		return;
	} //end if

	const now = new Date().getTime();
	setExperimentsWithPageViewsCookie( {
		...getExperimentsWithPageViews(),
		...events.reduce( ( result, event ) => {
			result[ event.experiment ] = now;
			return result;
		}, {} as ExpsWithViews ),
	} );
} //end updateExperimentsWithPageViews()

export function canSynchEvent( event: TrackEvent ): boolean {
	if ( 'visit' === event.kind ) {
		return true;
	} //end if

	const experimentsWithPageViews = getExperimentsWithPageViews();
	if ( ! experimentsWithPageViews[ event.experiment ] ) {
		return false;
	} //end if

	return (
		'conversion' !== event.kind ||
		canSynchConversion( event.experiment, event.goal )
	);
} //end canSynchEvent()

export function getExperimentsWithPageViews(): ExpsWithViews {
	try {
		const value = getCookie( 'nabExperimentsWithPageViews' ) ?? '';
		return JSON.parse( value ) as ExpsWithViews;
	} catch ( e ) {
		return {};
	} //end try
} //end getExperimentsWithPageViews()

// =======
// HELPERS
// =======

let synchedGoals: ReadonlyArray< string > = [];
function canSynchConversion(
	experimentId: ExperimentId,
	goalIndex: GoalIndex
): boolean {
	if ( synchedGoals.includes( `${ experimentId }-${ goalIndex }` ) ) {
		return false;
	} //end if

	synchedGoals = [ ...synchedGoals, `${ experimentId }-${ goalIndex }` ];
	return true;
} //end canSynchConversion()

function setExperimentsWithPageViewsCookie( value: ExpsWithViews ): void {
	setCookie( 'nabExperimentsWithPageViews', JSON.stringify( value ), {
		expires: 120,
	} );
} //end setExperimentsWithPageViewsCookie()

let wasCookieCleaned = false;
function removeOldExperimentsFromCookie( experiments ): void {
	if ( wasCookieCleaned ) {
		return;
	} //end if
	wasCookieCleaned = true;

	const experimentsWithPageViews = getExperimentsWithPageViews();
	const experimentIds = Object.keys( experiments ).map( numberify );
	const experimentIdsWithPageViews = Object.keys(
		experimentsWithPageViews
	).map( numberify );

	const invalidExperimentIds = experimentIdsWithPageViews.filter(
		( id ) => ! experimentIds.includes( id )
	);

	const now = new Date().getTime();
	const TEN_DAYS_IN_MINUTES = 14400;
	const newExperimentsWithPageViews = experimentIdsWithPageViews.reduce(
		( result, id ) => {
			const expirationDate = experimentsWithPageViews[ id ];

			if ( invalidExperimentIds.includes( id ) ) {
				const minutesToLastValidation =
					Math.abs( now - expirationDate ) / 60000;
				if ( TEN_DAYS_IN_MINUTES < minutesToLastValidation ) {
					return result;
				} //end if

				return { ...result, [ id ]: expirationDate };
			} //end if

			return { ...result, [ id ]: now };
		},
		{} as ExpsWithViews
	);

	setExperimentsWithPageViewsCookie( newExperimentsWithPageViews );
} //end removeOldExperimentsFromCookie()

const numberify = ( x: string ) => Number.parseInt( x ) || 0;
