/**
 * External dependencies
 */
import 'es6-symbol';

/**
 * Internal dependencies
 */
import { showPage } from './redirection';
import { getExpectedAlternativeUrl } from './url';

import { doSingleAction } from '../../utils/actions';
import { doesVisitorParticipate } from '../../utils/participation';
import { removeQueryArgs } from '../../utils/url';
import {
	isVisitorInASegmentOfEveryTest,
	updateSegmentationSettings,
} from '../../utils/segments';
import { setCookie } from '../../utils/cookies';
import type { Settings } from '../types';

export const loader = { init };

let didInitRun = false;
async function init( settings: Settings ): Promise< void > {
	if ( didInitRun ) {
		return;
	} //end if
	didInitRun = true;

	settings = {
		...settings,
		currentUrl: window.location.href, // phpcs:ignore
		untestedUrl: removeQueryArgs( window.location.href, 'nab' ), // phpcs:ignore
	};

	const participates = doesVisitorParticipate( settings );
	if ( ! participates ) {
		const { untestedUrl } = settings;
		return showPage( settings, untestedUrl );
	} //end if

	if ( settings.isStagingSite ) {
		setCookie( 'nabIsStagingSite', true );
	} //end if

	const { experiments } = settings;
	await updateSegmentationSettings( settings, 'now' );
	if ( ! isVisitorInASegmentOfEveryTest( experiments ) ) {
		const { untestedUrl } = settings;
		return showPage( settings, untestedUrl );
	} //end if

	const alternativeUrl = getExpectedAlternativeUrl( settings );
	return showPage( settings, alternativeUrl );
} //end init()

doSingleAction( 'alt-loader-init-available' );
