export function addSingleAction( name: string, callback: () => void ): void {
	if ( ! hasAddSingleAction( window ) ) {
		// eslint-disable-next-line
		console.error(
			'Nelio A/B Testing - Helper function missing: nabAddSingleAction'
		);
		return;
	} //end if
	window.nabAddSingleAction( name, callback );
} //end addSingleAction()

export function doSingleAction( name: string ): void {
	if ( ! hasDoSingleAction( window ) ) {
		// eslint-disable-next-line
		console.error(
			'Nelio A/B Testing - Helper function missing: nabDoSingleAction'
		);
		return;
	} //end if
	setTimeout( () => {
		if ( hasDoSingleAction( window ) ) {
			window.nabDoSingleAction( name );
		} //end if
	}, 1 );
} //end doSingleAction()

const hasAddSingleAction = (
	w: unknown
): w is { nabAddSingleAction: ( a: string, fn: () => void ) => void } =>
	!! w && 'object' === typeof w && 'nabAddSingleAction' in w;

const hasDoSingleAction = (
	w: unknown
): w is { nabDoSingleAction: ( a: string ) => void } =>
	!! w && 'object' === typeof w && 'nabDoSingleAction' in w;
