/**
 * External dependencies
 */
import type { Url } from '@nab/types';

/**
 * Internal dependencies
 */
import type { PreloadQueryArgUrl } from '../types';

export default function preloadQueryArgs(
	config: ReadonlyArray< PreloadQueryArgUrl >
): void {
	if ( ! config.length ) {
		return;
	} //end if

	// eslint-disable-next-line @typescript-eslint/prefer-regexp-exec
	const matches = document.cookie.match( /nabAlternative=[0-9]+/ ) || [ '' ];
	const alt: number | typeof NaN = Number.parseInt(
		matches[ 0 ].split( '=' )[ 1 ]
	);
	if ( isNaN( alt ) ) {
		return;
	} //end if

	const site = document.location.protocol + '//' + document.location.hostname;
	const anchors = Array.from(
		document.querySelectorAll< HTMLAnchorElement >( 'a' )
	)
		.filter( ( a ) => a.href )
		.filter( ( a ) => 0 === a.href.indexOf( site ) )
		.filter( ( a ) => -1 === a.href.indexOf( '#' ) )
		.filter( ( a ) => -1 === a.href.indexOf( '/wp-content/' ) );

	config = [ ...config ].sort( ( x ) => ( x.type === 'alt-urls' ? -1 : 1 ) );
	config.forEach( ( c ) => {
		const scope = c.type === 'alt-urls' ? c.altUrls : c.scope;
		anchors
			.filter( ( a ) => -1 === a.href.indexOf( 'nab=' ) )
			.filter( ( a ) => isInScope( scope, a.href ) )
			.forEach( ( a ) => {
				const nab = alt % c.altCount;
				const url = c.type === 'alt-urls' ? c.altUrls[ nab ] : a.href;
				a.href = url.includes( '?' )
					? `${ url }&nab=${ nab }`
					: `${ url }?nab=${ nab }`;
			} );
	} );
} //end preloadQueryArgs()

// =======
// HELPERS
// =======

function isInScope( scope: ReadonlyArray< string >, link: string ): boolean {
	link = link.toLowerCase();
	return scope.some( ( l ) =>
		isPartialUrl( l ) ? link.includes( getPart( l ) ) : link === l
	);
} //end isInScope()

function isPartialUrl( url: Url ): boolean {
	return url.startsWith( '*' ) && url.endsWith( '*' );
} //end isPartialUrl()

function getPart( partialUrl: string ): string {
	return partialUrl.substring( 1, partialUrl.length - 1 );
} //end getPart()
