/**
 * Internal dependencies
 */
import { getCookie, setCookie } from '../../utils/cookies';
import type { Settings } from '../types';

export function getAlternative( settings: Settings ): number {
	const { maxCombinations } = settings;
	const nabAlt = Number.parseInt( getCookie( 'nabAlternative' ) ?? '' );
	let alternative: number = isNaN( nabAlt ) ? -1 : nabAlt;
	if ( alternative < 0 || alternative >= maxCombinations ) {
		alternative = random( maxCombinations - 1 );
		setCookie( 'nabAlternative', alternative, { expires: 120 } );
		setCookie( 'nabExperimentsWithPageViews', '{}', { expires: 120 } );
	} //end if

	const newAlternative = shouldAlternativeBeOverwritten( alternative );
	if ( false !== newAlternative ) {
		alternative = newAlternative;
		setCookie( 'nabAlternative', newAlternative, { expires: 120 } );
		setCookie( 'nabExperimentsWithPageViews', '{}', { expires: 120 } );
	} //end if

	setCookie( 'nabAlternative', getCookie( 'nabAlternative' ) ?? '', {
		expires: 120,
	} );
	setCookie(
		'nabExperimentsWithPageViews',
		getCookie( 'nabExperimentsWithPageViews' ) ?? '{}',
		{ expires: 120 }
	);

	return alternative;
} //end getAlternative()

// =======
// HELPERS
// =======

function random( max: number ) {
	return Math.min( Math.floor( Math.random() * ( max + 1 ) ), max );
} //end random()

function shouldAlternativeBeOverwritten(
	assignedAlternative: number
): number | false {
	const search = document.location.search;
	if ( ! /\bnabforce\b/.test( search ) ) {
		return false;
	} //end if

	if ( ! /\bnab\b=[0-9]+/.test( search ) ) {
		return false;
	} //end if

	const expectedAlternative =
		Number.parseInt( search.replace( /^.*\bnab\b=([0-9]+).*$/, '$1' ) ) ||
		0;
	if ( expectedAlternative === assignedAlternative ) {
		return false;
	} //end if

	return expectedAlternative;
} //end shouldAlternativeBeOverwritten()
